.arrowUp{
    position: fixed; 
    width: 100%;
    left: 90%;
    bottom: 40px;
    height: 3rem;
    font-size: 3rem;
    z-index: 1;
    cursor: pointer;
    color: #1dabc7;
    
    width: 3rem;
    border-radius: 50%;
}

@media (max-width:500px){
    .arrowUp{
        left:80%;
    }
}